.container{
  text-align: center;
  border-radius: 5px;
  background-color: #f2f2f2;
  margin: 10vh 35vw 10vh 35vw;
  padding-bottom: 3vh;
  border-radius: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); 
}

.height{
  margin-top: 2vh;
}

.buttons{
  display: flex;
  flex-direction: row;
  margin-top: 1vh;
  margin-left: 10vw;
}

button{
  margin: 1vw;
}

.name{
  margin-bottom: 1vh;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 768px) {
  .container {
    margin: 10vh 5vw;
    padding-bottom: 2vh;
    
  }

  .height {
    margin-top: 1vh;
  }

  .buttons{
    
  margin-left: 30vw;
  }
  
  button {
    margin: 5px;
  }
}

@media screen and (max-width: 480px) {
  .container {
    margin: 10vh 10vw;
    padding-bottom: 1vh;
  }

  .name{
    margin-left: 0px;
  }
  
  .height {
    margin-top: 1vh;

  }

  .buttons{
    display: flex;
    flex-direction: row;
    margin-left: 20vw;
    gap: 10px;
    margin-top: 1vh;
  }
  
  button {
    margin: 2px;
    margin-top: 1.5vh;
  }
  .name{
    margin-bottom: 1vh;
  }
    
}